import { format } from 'd3-format';
import React, { memo, useCallback, useMemo } from 'react'
import { VictoryBar, VictoryGroup } from 'victory';
import ChartBase from './ChartBase';

import theme from './theme';

const Bar = ({ data, keys, radioToggle, barWidth, precision, ...props }) => {
  const pre = useMemo(() => typeof precision === 'number' ? precision : data[0].values[0].y.toString().split('.')[1] ? 1 : 0 , [data, precision])
  const formatter = useMemo(() => pre > 0 ? format(`.${pre}f`) : format(',d'), [pre])

  const keysColor = useMemo(() => keys.reduce((kc, k, i) => {
    kc[k] = theme.group.colorScale[i]
    return kc
  }, {}), [keys])
  return (
    <ChartBase
      // tooltipLabels={radioToggle ? (({ datum }) => `${datum.childName}\n${datum.y}`) : undefined}
      // keysColor={keysColor}
      {...props}
    >
      <VictoryGroup offset={barWidth}>
        {data.map(({ key, values }, i) => {
          return (
            <VictoryBar
              key={key + i}
              name={key}
              data={values}
              barWidth={barWidth}
              style={{
                data: { fill: radioToggle ? undefined : keysColor[key] },
                labels: { fontSize: radioToggle ? 9.5 : 12 }
              }}
              labels={({ datum }) => formatter(datum.y)}
            />
          );
        })}
      </VictoryGroup>

    </ChartBase>
  )
}

export default memo(Bar)
