import React, { createElement, memo, useMemo, useState } from 'react'
import { AspectRatio, Circle, Flex, HStack, Stack } from '@chakra-ui/layout'
import useSWR from 'swr'
import { useCheckbox, useCheckboxGroup } from '@chakra-ui/checkbox'
import { chunk, groupBy, last, mapValues, maxBy, size, sumBy } from 'lodash'
import { useRadio, useRadioGroup } from '@chakra-ui/radio'
import { Spinner } from '@chakra-ui/spinner'
import { useWindowSize } from 'react-use'
import styled from '@emotion/styled'

import chartData from '../../data'
import Text from '../../components/Text'
import Box from '../../components/Box'
import Carousel from '../../components/Carousel'
import { responsive, colorScale } from '../../components/ThemeProvider/theme'
import StackedBar from './StackedBar'
import Bar from './Bar'
import Line from './Line'
import BarAndLine from './BarAndLine'
import useResponsive from '../../contexts/mediaQuery/useResponsive'

const NoteWrapper = styled(Box)`
counter-reset: note-counter;
`

const fetcher = (...a) => fetch(...a).then(res => res.text())

const chartComponents = {
  stacked: StackedBar,
  'stacked-percent': StackedBar.Percent,
  bar: Bar,
  line: Line,
  'bar+line': BarAndLine,
}

const getXPadding = count => {
  if (count < 3) return 100
  if (count < 4) return 60
  return 40
}

const getBarWidth = count => {
  if (count < 4) return 32
  if (count < 6) return 25
  return 15
}

const ButtonBase = ({ input, checkbox, color, children, rootValue }) => (
  <Box as="label" m="0.25em">
    <input
      {...input}
      onChange={e => {
        if ((rootValue && rootValue.length > 1) || !input.checked) {
          input.onChange(e)
        }
      }}
    />
    <Box
      {...checkbox}
      cursor="pointer"
      borderColor="white"
      borderWidth="1px"
      color={color}
      borderRadius="full"
      _checked={{
        opacity: 1,
      }}
      opacity={0.35}
      px={2}
      pb={0.5}
      fontSize={responsive('0.875em', '1.125em')}
      fontWeight="700"
    >
      {children}
    </Box>
  </Box>
)

const ToggleBase = ({ children, categoryPerLine, ...props }) => {
  const { isMobile } = useResponsive()
  return (
    <Flex justify="center" {...props}>
      <Stack
        direction={responsive('column', 'row')}
        justifyContent="center"
        spacing={responsive('0', '0.5rem')}
        alignItems={responsive('flex-start', 'center')}
      >
        <Box>
          <Text fontSize={responsive('0.625em', '0.875em')}>請依類別篩選</Text>
        </Box>
        <Box>
          {useMemo(() => chunk(children, categoryPerLine && isMobile ? categoryPerLine : Infinity).map((row, i) => (
            <Flex flexWrap="wrap" mx="-0.25em" key={i}>
              {row}
            </Flex>
          )), [categoryPerLine, children, isMobile])}
        </Box>
      </Stack>
    </Flex>
  )
}

const CheckBoxButton = ({ color, rootValue, ...props }) => {
  const { getInputProps, getCheckboxProps } = useCheckbox(props)

  const input = getInputProps()
  const checkbox = getCheckboxProps()
  return <ButtonBase input={input} checkbox={checkbox} color={color} rootValue={rootValue} {...props} />
}

const CategoryToggler = ({ options, onChange, categoryPerLine, defaultValue }) => {
  const { getCheckboxProps, value } = useCheckboxGroup({
    name: "categories",
    defaultValue: defaultValue || options,
    // onChange: v => onChange(v.length ? v : options),
    onChange,
  })
  return (
    <ToggleBase categoryPerLine={categoryPerLine}>
      {options.map((d, i) => {
        const checkbox = getCheckboxProps({ value: d })
        return <CheckBoxButton key={d} color={colorScale[i]} rootValue={value} {...checkbox}>{d}</CheckBoxButton>
      })}
    </ToggleBase>
  )
}

const RadioButton = ({ color, ...props }) => {
  const { getInputProps, getCheckboxProps } = useRadio(props)

  const input = getInputProps()
  const checkbox = getCheckboxProps()
  return <ButtonBase input={input} checkbox={checkbox} color={color} {...props} />

}

const CategoryRadioToggler = ({ options, onChange }) => {
  const { getRootProps, getRadioProps } = useRadioGroup({
    name: "categories",
    defaultValue: options[0],
    onChange: v => onChange([v]),
    // onChange,
  })
  const rootProps = getRootProps()
  return (
    <ToggleBase {...rootProps}>
      {options.map((d) => {
        const checkbox = getRadioProps({ value: d })
        return <RadioButton key={d} color={colorScale[2]} {...checkbox}>{d}</RadioButton>
      })}
    </ToggleBase>
  )
}

const getYMax = stacked => (d) => {
  const group = groupBy(d.reduce((all, dd) => [...all, ...dd.values], []), 'x')
  return Math.max(...Object.values(mapValues(group, g => stacked ? sumBy(g, 'y') : maxBy(g, 'y').y)))
}

const useChartHeight = () => {
  const { height } = useWindowSize()
  return useMemo(() => {
    if (height < 600) return 200
    return 250
  }, [height])
}

const Chart = memo(({
  togglable,
  radioToggle,
  type,
  file,
  notes,
  noXUnit,
  biaxial,
  categoryPerLine,
  legend,
  precision,
  defaultFilter,
  yMax,
}) => {
  const chartHeight = useChartHeight()
  const { data = null } = useSWR(file, fetcher)
  const { isMobile } = useResponsive()
  const formattedData = useMemo(() => {
    if (!data) return null
    const rows = data.split(/\r?\n/).map(r => r.split(','))
    const header = rows.shift()
    const unit = header[0]
    let unit2
    const xUnit = last(header) < 13 ? '月' : '年'
    const grouped = Number.isNaN(rows[0][1] * 1)
    let total
    const keys = {}
    const d = rows.map((v) => {
      const key = v[0]
      if (biaxial) {
        const vv = v.slice(1)
        if (vv.length && !vv.some(Boolean)) {
          unit2 = key
          return null
        }
      }
      const group = grouped ? v[1] : null
      const index = grouped ? 2 : 1
      const f = {
        values: v.slice(index).map((vv, i) => ({ x: header[index + i], y: vv * 1 })),
        key,
        group,
        unit2,
      }
      if (!key) return null
      if (key === '總和') {
        total = f
        return null
      }
      keys[key] = 1
      return f
    }).filter(Boolean)
    const sidedData = biaxial ? Object.values(groupBy(d, 'unit2')) : d

    return {
      xUnit,
      unit,
      unit2,
      data: biaxial ? sidedData[0] : d,
      data2: biaxial && sidedData[1],
      grouped,
      total,
      xPadding: getXPadding(d[0].values.length),
      barWidth: getBarWidth(d[0].values.length) * (radioToggle ? 0.5 : 1),
      keys: type !== 'line' && !radioToggle && legend ? legend : Object.keys(keys),
      yMax: yMax || (biaxial ? sidedData.map(getYMax(type === 'stacked')): getYMax(type === 'stacked')(d)),
    }
  }, [data, biaxial, legend, type, radioToggle, yMax])

  const [activeCategories, setCategories] = useState(defaultFilter)

  return (
    <>
      {togglable && formattedData && <CategoryToggler defaultValue={defaultFilter} categoryPerLine={categoryPerLine} options={formattedData.keys} onChange={setCategories} />}
      {radioToggle && formattedData && <CategoryRadioToggler categoryPerLine={categoryPerLine} options={formattedData.keys} onChange={setCategories} />}
      <AspectRatio ratio={400 / chartHeight}>
        <Box border="1px solid white" p="0.5em">
          {formattedData ? createElement(chartComponents[type] || 'div', {
            ...formattedData,
            animate: togglable,
            noXUnit,
            radioToggle,
            precision,
            width: 400,
            height: chartHeight,
            isMobile,
            data: formattedData.data.map((d) => {
              const active = (radioToggle ? !activeCategories && d.key === formattedData.keys[0] : !activeCategories) || activeCategories?.includes(d.key)
              if (radioToggle && !active) return null
              return { ...d, values: d.values.map(dd => ({ ...dd, y: active ? dd.y : 0 })) }
            }).filter(Boolean),
            filtered: activeCategories && activeCategories.length !== formattedData.keys.length,
          }) : <Spinner />}
        </Box>
      </AspectRatio>
      {legend && (
        <HStack
          justifyContent="center"
          spacing={radioToggle ? responsive('0.25em', '0.5em') : '1em'}
          fontSize={radioToggle ? responsive('0.625em', '0.875em') : responsive('0.875em', '1em')}
        >
          {legend.map((label, i) => (
            <HStack
              color={type === 'line' ? 'white' : colorScale[i]}
              spacing={radioToggle ? '0.125em' : '0.375em'}
            >
              {type === 'line' ? (
                <Box
                  mt="0.125em"
                  width="1.75em"
                  height="4px"
                  rounded="1em"
                  bg={i ? 'black' : 'white'}
                  border="1px solid"
                  borderColor={i ? 'white' : 'black'}
                />
              ) : (
                <Circle mt="0.125em" size="0.625em" bg="current" />
              )}
              <Text fontWeight="500">{label}</Text>
            </HStack>
          ))}
        </HStack>
      )}

      {notes && (
        <NoteWrapper>
          {notes.map((n, i) => <Text.Note key={i}>{n}</Text.Note>)}
        </NoteWrapper>
      )}
    </>
  )
})

const ChartLayout = ({ title, ...props }) => {
  return (
    <Stack spacing="0.5em" px="0.25em">
      <Text.H3 textAlign="center" fontSize={responsive(title.length > 20 ? '0.875em' : '1.125em', '1.375em')}>{title}</Text.H3>
      <Chart {...props} />
    </Stack>
  )
}

const Charts = ({ data, ...props }) => {
  const charts = data.map(k => <ChartLayout key={k} {...chartData[k]} />)
  if (charts.length === 1) return <Box {...props}>{charts}</Box>
  return (
    <Box {...props}>
      <Carousel
        width="100%"
        silderProps={{
          infinite: false,
          lazyLoad: true,
        }}
        charts
      >{charts}</Carousel>
    </Box>
  )
}

export default Charts
