import { format } from 'd3-format';
import { groupBy, mapValues, reduce, round, size, sumBy } from 'lodash';
import React, { memo, useCallback, useMemo } from 'react'
import { VictoryBar, VictoryLabel, VictoryStack } from 'victory';

import ChartBase from './ChartBase';
import theme from './theme';

const p = format('.0%')

const StackedBar = memo(({ data, total, keys, filtered, yMax, valueFormat, precision, ...props }) => {
  const keysColor = useMemo(() => keys.reduce((kc, k, i) => {
    kc[k] = theme.group.colorScale[i]
    return kc
  }, {}), [keys])
  const totalLabels = useMemo(() => total?.values.map(t => valueFormat ? valueFormat(t.y) : t.y), [total, valueFormat])
  const keyedTotal = useMemo(() => total?.values.reduce((t, { x }, i) => {
    t[x] = totalLabels[i]
    return t
  }, {}), [total, totalLabels])

  const pre = useMemo(() => typeof precision === 'number' ? precision : size(totalLabels ? totalLabels[0].toString().split('.')[1] : 0) , [totalLabels, precision])
  const formatter = useMemo(() => pre > 0 ? format(`.${pre}f`) : format(',d'), [pre])
  const sumLabels = useMemo(() => {
    const group = groupBy(data.reduce((all, dd) => [...all, ...dd.values], []), 'x')
    return Object.values(mapValues(group, g => valueFormat ? valueFormat(sumBy(g, 'y')) : formatter(sumBy(g, 'y'))))
  }, [data, formatter, valueFormat])
  const labels = filtered ? sumLabels : totalLabels?.map(formatter)
  const getFill = useCallback((key, id) => id ? 'white' : keysColor[key], [keysColor])
  return (
    <ChartBase
      domain={{ y: [0, yMax] }}
      tooltipLabels={({ datum }) => datum.y ? `${datum.childName}\n${valueFormat ? valueFormat(datum.y) : formatter(datum.y)}${keyedTotal ? `(${p(datum.y / keyedTotal[datum.x])})` : ''}` : ''}
      tickFormat={valueFormat}
      yMax={yMax}
      keysColor={keysColor}
      {...props}
    >
      <VictoryStack labels={labels}>
        {data.map(({ key, values }, i) => {
          return (
            <VictoryBar
              key={key + i}
              name={key}
              data={values}
              style={{ data: { fill: keysColor[key] }, labels: { fill: d => getFill(key, d.id), fontSize: 14 } }}
              barRatio={0.8}
            />
          );
        }).reverse()}
      </VictoryStack>
    </ChartBase>
  )
})

StackedBar.Percent = props => <StackedBar {...props} valueFormat={format('.0%')} />

export default StackedBar
