import React, { memo, useMemo } from 'react'
import { VictoryLine } from 'victory';
import ChartBase from './ChartBase';

import theme from './theme';

const Line = ({ data, keys, grouped, ...props }) => {
  const keysColor = useMemo(() => keys.reduce((kc, k, i) => {
    kc[k] = theme.group.colorScale[i]
    return kc
  }, {}), [keys])
  const groupStroke = useMemo(() => {
    if (!grouped) return {}
    const groups = data.reduce((gps, d, i) => {
      if (typeof gps[d.group] === 'undefined') {
        gps[d.group] = i
      }
      return gps
    }, {})
    return groups
  }, [grouped, data])
  return (
    <ChartBase
      {...props}
      tooltipLabels={({ datum }) => {
        const [g, n] = datum.childName.split('-')
        return datum.y ? `${g}/${datum.x}月\n${n} - ${datum.y}` : ''
      }}
      keysColor={keysColor}
    >
      {data.map(({ key, values, group }, i) => {
        const onlyStroke = !groupStroke[group]
        const l = (
          <VictoryLine
            key={key + i}
            name={`${group}-${key}`}
            data={values}
            style={{ data: { stroke: keysColor[key], strokeWidth: 4 }, labels: { fill: keysColor[key] } }}
          />
        )
        // return l
        return onlyStroke ? (
          [
            l,
            <VictoryLine
              key={`${key}-${i}-ghost`}
              name="ghost"
              data={values}
              style={{ data: { stroke: 'black' } }}
            />
          ]
        ): l;
      })}

    </ChartBase>
  )
}

export default memo(Line)
