import React, { useMemo } from 'react'
import { VictoryAxis, VictoryChart, VictoryLabel, VictoryTooltip, VictoryVoronoiContainer } from 'victory';

import theme from './theme';

const ChartBase = ({
  children,
  xUnit,
  unit,
  xPadding,
  yMax,
  tooltipLabels,
  domain,
  noXUnit,
  padding = {},
  tickValues,
  tickFormat,
  animate,
  width,
  height,
  isMobile,
}) => {
  const yAxisWidth = useMemo(() => {
    if (yMax < 100) return 3.5
    if (yMax < 1000) return 4
    return 5
  }, [yMax])
  const axisUnit = useMemo(() => isMobile ? unit.replace(/（([^）]+)）/, '／$1') : unit, [isMobile, unit])
  return (
    <VictoryChart
      domainPadding={{ x: xPadding, y: 20 }}
      theme={theme}
      domain={domain}
      width={width}
      height={height}
      animate={animate && { duration: 500 }}
      padding={{ ...theme.chart.padding, ...padding, left: yAxisWidth * 13 }}
      containerComponent={tooltipLabels &&
        <VictoryVoronoiContainer
          // mouseFollowTooltips
          labels={tooltipLabels}
          labelComponent={<VictoryTooltip constrainToVisibleArea />}
          voronoiBlacklist={['ghost']}
        />
      }
    >
      <VictoryAxis
        style={{ grid: { stroke: 'none' }, axisLabel: { padding: 8 } }}
        label={!noXUnit && `(${xUnit})`}
        axisLabelComponent={<VictoryLabel dx={160}/>}
      />
      <VictoryAxis
        dependentAxis
        tickValues={tickValues}
        tickFormat={tickFormat}
        label={axisUnit}
        style={{
          axis: { stroke: 'none' },
          axisLabel: {
            writingMode: 'tb',
            // textOrientation: 'mixed',
            textAnchor: 'start',
          }
        }}
        axisLabelComponent={<VictoryLabel verticalAnchor="start" x={8} y={20} angle={0} />}
      />
      {children}
    </VictoryChart>
  )
}

export default ChartBase
